<template>
<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
  <main class="content container" v-if="infoData">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'cart'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> {{ infoData.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;    &nbsp; .
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ infoData.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ infoData.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ infoData.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ infoData.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ infoData.paymentType }}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in infoData.basket.items" :key="item.id">
              <h3>{{ item.product.title }} <span v-if="item.quantity > 1" style="color: #000">X{{ item.quantity }}</span></h3>
              <b>{{ item.product.price * item.quantity | numberFormat}} </b>
              <span>: 150030</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>{{ infoData.deliveryType.price | numberFormat}} </b></p>
            <p>: <b>{{infoData.basket.items.length}}</b> {{ pluralize }}   <b>{{ infoData.totalPrice | numberFormat}} </b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
/* eslint-disable max-len */
import numberFormat from '@/helpers/numberFormat';
import wordFormat from '@/helpers/wordFormat';

export default {
  filters: {
    numberFormat,
  },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) return;
    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
  computed: {
    infoData() {
      return this.$store.state.orderInfo;
    },
    pluralize() {
      return wordFormat(this.infoData.basket.items.length);
    },
  },
};
</script>
